<template>
  <div class="signlifeprint">
    <div>
        <!-- 畫面顯示內容區域 -->
        <h3>{{cData.company_name}}</h3>

        <div class="text-center border-bottom my-2">
          <h1>
            <strong>個案服務紀錄表</strong>
          </h1>
        </div>

        <table class="form_title" border="3px #000000 solid;" width ="100%">
          <thead>
            <tr>
              <th colspan="5" class="p-2">
                <v-row>
                  <v-col cols="2">病歷號: {{uData.p_code}}</v-col>
                  <!-- <v-col cols="2">床號: {{uData.bed_name}}</v-col> -->
                  <v-col cols="2">姓名: {{uData.name}}</v-col>
                  <v-col cols="2">性別: {{uData.sex}}</v-col>
                  <v-col cols="3">教育程度: {{education}}</v-col>
                  <v-col cols="3">步態: {{action}}</v-col>
                </v-row>
              </th>
            </tr>
          </thead>
          
          <tbody class="text-center" border="4px #000000 solid;" v-for="item in items" :key="item.snkey">
            <tr>
              <td colspan="5" class="grey lighten-3"></td>
            </tr> 
            <tr>
              <th>評估日期</th>
              <th>服務對象</th>
              <th>服務類型</th>
              <th>服務地點</th>
              <th>服務模式</th>
            </tr>
            <tr>
              <td class="text-no-wrap">{{item.date}}</td>
              <td>{{item.target}}</td>
              <td>{{item.type}}</td>
              <td>{{item.location}}</td>
              <td>{{item.model}}</td>
            </tr>
            <tr>
              <th>近況簡述</th>
              <td colspan="4" class="text-left">{{item.simplecontent}}</td>
            </tr>  
            <tr>
              <th>下次計畫</th>
              <td colspan="4" class="text-left">{{item.nextplan}}</td>
            </tr>  
            <tr>
              <td colspan="5" class="text-right pr-2 ">紀錄人:{{item.create_man | filterName}}</td> 
            </tr> 
          </tbody>


        </table>
      
      <!-- <pre>{{items}}</pre> -->
    </div>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      cData: {},
      pData: {},
      uData: {},
      list: {}, //表單內資料
      items:[],
      nursingassessmentItems: {},
      educationItems:{
        education1:'不識字',
        education2:'國小以下',
        education3:'小學',
        education4:'國中',
        education5:'高中(職)',
        education6:'大學/大專',
        education7:'大學以上',
        education8:'不確定',
      },
      actionItems:{
        action3:'穩定',
        action4:'不穩定',
        action5:'無法站立',
        action6:'不自主運動',
      },
      
      // nutritionItems: [],
      // recordItems: []
    };
  },

  filters: {
    filterName(val) {
      if (val != undefined) {
        var name = val.split("(");
        return name[0];
      }
    },
    
    
  },

  computed: {
    age() {
      return dayjs().format("YYYY") - dayjs(this.uData.birthday).format("YYYY");
    },
    education(){
      var postStr = "";
      for (var i in this.nursingassessmentItems){
        if (i.includes('education') && this.nursingassessmentItems[i] == "true"){
          postStr += this.educationItems[i]+';';
        }
      }
      return postStr;
    },
    action(){
      var postStr = "";
      for (var i in this.nursingassessmentItems){
        if (i.includes('action') && this.nursingassessmentItems[i] == "true"){
          postStr += this.actionItems[i]+';';
        }
      }
      
      if (this.nursingassessmentItems['action7'] != '' && this.nursingassessmentItems['action7'] != undefined){
        postStr += this.nursingassessmentItems['action7'];
      }
      
      return postStr;
    },
    
  },

  mounted() {
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    this.pData = JSON.parse(sessionStorage.getItem("pData"));
    this.uData = JSON.parse(sessionStorage.getItem("uData"));
    // this.list = JSON.parse(sessionStorage.getItem("printData"));
    this.items = JSON.parse(sessionStorage.getItem("printData"));
    this.getNursingassessment();
  },

  methods: {
    getNursingassessment() {
      var postData = {
        key: "u_snkey",
        value: this.uData.snkey
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/nursingassessment",
          qs.stringify(postData)
        )
        .then(rs => {
          // console.log(rs.data)
          if (rs.data.length > 0){
            this.nursingassessmentItems = JSON.parse(rs.data[0].datalist);
          }
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
  }
};
</script>

<style lang="scss" scoped>
.signlifeprint {
  @import "node_modules/bootstrap/scss/bootstrap";
  @import "node_modules/bootstrap-vue/src/index.scss";

  .form_title {
    font-size: 1.2rem;
    // text-align: center;
    font-family: "微軟正黑體";
  }
  
  td{
    padding:4px;
    border:1px #000000 solid;
  }
}
</style>